


import 'date-fns';
import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { formValues, formTouched, formErrors } from 'components/__types__/productSubscription';
import { withFormik } from 'formik';
import { withTranslation } from 'react-i18next';
import { withStyles } from '@material-ui/core/styles';
import { compose } from 'recompose';
import * as Yup from 'yup';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import { DateTimePicker, MuiPickersUtilsProvider } from '@material-ui/pickers';
import DateFnsUtils from '@date-io/date-fns';
import dateFnsLocales from 'i18n/dateFnsLocales';
import ConfirmationDialogTrigger from 'components/common/ConfirmationDialogTrigger';

const styles = theme => ({
  root: {
    margin: theme.spacing(3),
  },
  textField: {
    width: '100%',
  },
});
class ProductSubscriptionForm extends PureComponent {
  constructor(props) {
    super(props);
    this.handleConfirmationDialogAction = this.handleConfirmationDialogAction.bind(this);
  }

  handleConfirmationDialogAction(action) {
    const { onDelete, values } = this.props;
    switch (action) {
      case ConfirmationDialogTrigger.CONFIRM: {
        onDelete(values);
        break;
      }
      default:
        break;
    }
  }

  render() {
    const {
      classes,
      values,
      touched,
      errors,
      handleChange,
      handleBlur,
      handleSubmit: formikHandleSubmit,
      onDelete,
      onCancelEditing,
      isSubmitting,
      setFieldValue,
      t,
      i18n,
    } = this.props;

    const handleDateChange = field => value => {
      setFieldValue(field, value);
    };

    const dateTimeLabelFn = date => (date ? new Date(date).toLocaleString(i18n.language) : '');
    const getHelperText = field => (errors[field] && touched[field] ? errors[field] : '');


    const handleSubmit = e => {
      e.stopPropagation(); // avoids double submission caused by react-shadow-dom-retarget-events
      formikHandleSubmit(e);
    };

    return (
      <MuiPickersUtilsProvider utils={DateFnsUtils} locale={dateFnsLocales[i18n.language]}>
        <form onSubmit={handleSubmit} className={classes.root} data-testid="productSubscription-form">
          <Grid container spacing={2}>
            <Grid item xs={12} sm={6}>
              <TextField
                id="productSubscription-level"
                error={errors.level && touched.level}
                helperText={getHelperText('level')}
                className={classes.textField}
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.level}
                name="level"
                label={t('entities.productSubscription.level')}
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <TextField
                id="productSubscription-length"
                error={errors.length && touched.length}
                helperText={getHelperText('length')}
                className={classes.textField}
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.length}
                name="length"
                label={t('entities.productSubscription.length')}
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <TextField
                id="productSubscription-status"
                error={errors.status && touched.status}
                helperText={getHelperText('status')}
                className={classes.textField}
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.status}
                name="status"
                label={t('entities.productSubscription.status')}
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <DateTimePicker
                id="productSubscription-startDate"
                error={errors.startDate && touched.startDate}
                helperText={getHelperText('startDate')}
                className={classes.textField}
                onChange={handleDateChange('startDate')}
                value={values.startDate}
                labelFunc={dateTimeLabelFn}
                name="startDate"
                label={t('entities.productSubscription.startDate')}
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <DateTimePicker
                id="productSubscription-endDate"
                error={errors.endDate && touched.endDate}
                helperText={getHelperText('endDate')}
                className={classes.textField}
                onChange={handleDateChange('endDate')}
                value={values.endDate}
                labelFunc={dateTimeLabelFn}
                name="endDate"
                label={t('entities.productSubscription.endDate')}
              />
            </Grid>
            {onDelete && (
              <ConfirmationDialogTrigger
                onCloseDialog={this.handleConfirmationDialogAction}
                dialog={{
                  title: t('entities.productSubscription.deleteDialog.title'),
                  description: t('entities.productSubscription.deleteDialog.description'),
                  confirmLabel: t('common.yes'),
                  discardLabel: t('common.no'),
                }}
                Renderer={({ onClick }) => (
                  <Button onClick={onClick} disabled={isSubmitting}>
                    {t('common.delete')}
                  </Button>
                )}
              />
            )}

            <Button onClick={onCancelEditing} disabled={isSubmitting} data-testid="cancel-btn">
              {t('common.cancel')}
            </Button>

            <Button type="submit" color="primary" disabled={isSubmitting} data-testid="submit-btn">
              {t('common.save')}
            </Button>
          </Grid>
        </form>
      </MuiPickersUtilsProvider>
    );
  }
}

ProductSubscriptionForm.propTypes = {
  classes: PropTypes.shape({
    root: PropTypes.string,
    textField: PropTypes.string,
    submitButton: PropTypes.string,
    button: PropTypes.string,
    downloadAnchor: PropTypes.string,
  }),
  values: formValues,
  touched: formTouched,
  errors: formErrors,
  handleChange: PropTypes.func.isRequired,
  handleBlur: PropTypes.func.isRequired,
  handleSubmit: PropTypes.func.isRequired,
  onDelete: PropTypes.func,
  onCancelEditing: PropTypes.func,
  isSubmitting: PropTypes.bool.isRequired,
  setFieldValue: PropTypes.func.isRequired,
  t: PropTypes.func.isRequired,
  i18n: PropTypes.shape({ language: PropTypes.string }).isRequired,
};

ProductSubscriptionForm.defaultProps = {
  onCancelEditing: () => {},
  classes: {},
  values: {},
  touched: {},
  errors: {},
  onDelete: null,
};

const emptyProductSubscription = {
  level: '',
  length: '',
  status: '',
  startDate: null,
  endDate: null,
};


const validationSchema = Yup.object().shape({
  level: Yup.string(),
  length: Yup.string(),
  status: Yup.string(),
  startDate: Yup.date().nullable(),
  endDate: Yup.date().nullable(),
});

const formikBag = {
  mapPropsToValues: ({ productSubscription }) => productSubscription || emptyProductSubscription,

  enableReinitialize: true,

  validationSchema,

  handleSubmit: (values, { setSubmitting, props: { onSubmit } }) => {
    onSubmit(values);
    setSubmitting(false);
  },

  displayName: 'ProductSubscriptionForm',
};

export default compose(
  withStyles(styles, { withTheme: true }),
  withTranslation(),
  withFormik(formikBag)
)(ProductSubscriptionForm);
