import { getDefaultOptions, request } from 'api/helpers';

const resource = 'api/product-subscriptions';

export const apiProductSubscriptionGet = async (serviceUrl, id) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'GET',
  };
  return request(url, options);
};

export const apiProductSubscriptionPost = async (serviceUrl, productSubscription) => {
  const url = `${serviceUrl}/${resource}`;
  const options = {
    ...getDefaultOptions(),
    method: 'POST',
    body: productSubscription ? JSON.stringify(productSubscription) : null,
  };
  return request(url, options);
};

export const apiProductSubscriptionPut = async (serviceUrl, productSubscription) => {
  const url = `${serviceUrl}/${resource}`;
  const options = {
    ...getDefaultOptions(),
    method: 'PUT',
    body: productSubscription ? JSON.stringify(productSubscription) : null,
  };
  return request(url, options);
};

export const apiProductSubscriptionDelete = async (serviceUrl, id) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'DELETE',
  };
  return request(url, options);
};
